




















































import { Component, Vue } from 'vue-property-decorator'
import axios from 'axios'
import { sysConf, api } from '@/assets/ts/path.conf'

@Component({
  components: {
    Container: () => import('@/components/Container.vue'),
    Dialog: () => import('@/components/DialogWindow.vue'),
    CodeEditor: () => import('@/components/CodeEditorIDE.vue'),
    MarkDownEditor: () => import('@/components/MarkDownEditor.vue'),
    Terminal: () => import('@/components/Terminal.vue'),
    Browser: () => import('@/components/Browser.vue'),
    Game: () => import('@/components/Game.vue'),
    Site: () => import('@/components/Site.vue')
  }
})
export default class Home extends Vue {
  private SLASH !:any

  date :string = '' //时间
  mContent :string = '' //markdown内容
  mTitle :string = '' //markdown标题
  mPath :string = '' //markdown保存路径
  isManage :boolean = false //是否打开文件管理器
  isMark :boolean = false //是否打开markdown编辑器
  isCode :boolean = false //是否打开代码编辑器
  isTerminal :boolean = false //是否打开终端
  isGame :boolean = false //是否打开游戏
  isBrowser :boolean = false //是否打开浏览器
  isSite :boolean = false //是否打开站点

  selected :string = '' //当前激活的窗口
  status :string = '' //markdown编辑器状态
  dirSelectIndex :number = -1 //文件夹选中的索引
  fileSelectIndex :number = -1 //文件选中的索引
  fileList :object = {} //窗口内容区的文件列表
  menuSelectIndex :number = -1 //左侧菜单项选中的索引
  apiUrl :string = api.fileList //桌面文件列表查询api
  path :string = sysConf.desktop //桌面的路径
  tpath :string = sysConf.rootPath //管理器打开的路径

  //改变编辑器状态
  private changeMdStatus(status :string) :void {
    this.status = status
  }
  //打开文件管理器
  private fileManage() :void {
    this.selected = 'manage'
    this.isManage = true
    this.tpath = sysConf.rootPath
  }
  private terminal() :void {
    this.selected = 'terminal'
    this.isTerminal = true
  }
  //打开markdown编辑器
  private markdown() :void {
    this.selected = 'mark'
    this.isMark = true
  }
  //打开代码编辑器
  private code() :void {
    this.selected = 'code'
    this.isCode = true
  }
  //打开浏览器
  private browser() :void {
    this.isBrowser = true
  }
  //打开游戏
  private game() :void {
    this.isGame = true
  }
  private site() :void {
    this.isSite = true
  }
  //关闭文件管理器
  private closeManage() :void {
    this.isManage = false
  }
  //关闭markdown编辑器
  private closeMark() :void {
    this.isMark = false
    this.mContent = ''
    this.mTitle = ''
    this.mPath = ''
  }
  //关闭代码编辑器
  private closeCode() :void {
    this.isCode = false
  }
  //关闭终端
  private closeTerminal() :void {
    this.isTerminal = false
  }
  private closeBrowser() :void {
    this.isBrowser = false
  }
  //关闭游戏
  private closeGame() :void {
    this.isGame = false
  }
  private closeSite() :void {
    this.isSite = false
  }
  //点击窗口，使得窗口显示在最上面
  private clickEvent(type :string) :void {
    this.selected = type
  }

  //获取内容区的文件列表
  private getFileTree() :void {
    axios.post(this.apiUrl, {path: this.path}).then((res :any) => {
      this.fileList = res.data.data
    })
  }
  //双击打开文件夹
  private openFolder(name :string) :void {
    let folderPath = this.path + name + this.SLASH
    this.isManage = true
    this.tpath = folderPath
  }
  //选中文件夹时高亮
  private folderSelectHighlight(index :number) :void {
    this.fileSelectIndex = -1
    this.dirSelectIndex = index
  }
  //选中文件时高亮
  private fileSelectHighlight(index :number) :void {
    this.dirSelectIndex = -1
    this.fileSelectIndex = index
  }
  //桌面区域点击取消文件选中
  private homeClick() :void {
    this.dirSelectIndex = -1
    this.fileSelectIndex = -1
  }
  //打开文件
  private openFile(path :string, name :string) :void {
    let narr = name.split('.md')
    if(narr.length === 2) {
      axios.post(api.fileContent, {path: path + name}).then((res :any) => {
        this.mTitle = narr[0]
        this.mContent = res.data.data
        this.mPath = path
        this.markdown()
      })
    }
  }
  created() :void {
    setInterval(() => {
      this.date = new Date().toString().split("GMT")[0]
    }, 1000)
    this.getFileTree()
  }
}
